import {Link} from "react-router-dom";




const Nav = ()=>{
    return(
        <div className="container-fluid">
        <div className="row">
        <div className="col-12 p-0 m-0">
        <nav className="navbar navbar-expand-lg navbg pt-2 ps-5 ">
        <div className="container-fluid">
          <Link className="navbar-brand text-white fs-1 font1" to="#">Feane</Link>
          <button className="navbar-toggler bg-light" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mx-auto mb-2 mb-lg-0 ps-5">
              <li className="nav-item">
                <Link className="nav-link active px-3 text-warning home font2 fw-bold" aria-current="page" to="./Home">HOME</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link px-3 text-warning menu font2 fw-bold" aria-current="page" to="./Menu">MENU</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link px-3 text-warning about font2 fw-bold" aria-current="page" to="./About">ABOUT</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link px-3 text-warning book font2 fw-bold" aria-current="page" to="./Book">BOOK TABLE</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link px-3 text-warning book font2 fw-bold" aria-current="page" to="./"></Link>
              </li>
             


              
             
             
            </ul>
                 <div className="">
                 <i class="fas fa-user text-white pe-4"></i><i class="fas fa-search text-white pe-4"></i><i class="fas fa-shopping-cart text-white pe-4"></i><button type="button" class="btn btn-warning text-white rounded-pill px-5 py-2 pe-5 font2">Order Online</button>
                 </div>
          </div>
        </div>
      </nav>
      </div>
      </div> 
      </div> 
        
    );
}
export default Nav;