import React from "react";
import './Book';
const Book = () => {
    return (

        <div className="container">
        <div className="row pt-5 pb-5">
            <div className="col-12">
                <h2 className="font1 pb-3 fs-1">Book A Table</h2>
            </div>
            <div className="col-12 col-md-6">
                <form>
                    <div class="input-group mb-4">
                        <input type="text" class="form-control py-2" placeholder="Your Name" aria-label="Your Name" aria-describedby="basic-addon2"/>           
                    </div>
                    <div class="input-group mb-4">
                        <input type="text" class="form-control py-2" placeholder="Phone Number" aria-label="Phone Number" aria-describedby="basic-addon2"/>           
                    </div>
                    <div class="input-group mb-4">
                        <input type="text" class="form-control py-2" placeholder="Your Email" aria-label="Your Email" aria-describedby="basic-addon2"/>           
                    </div>
                    <select class="form-select mb-3 py-2" aria-label="Default select example">
                        <option selected>How Many Persons?</option>
                        <option value="1">2</option>
                        <option value="2">3</option>
                        <option value="3">4</option>
                    </select>
                    <div className="pt-3">
                    <input type="date" id="birthday" name="birthday" className="ps-2 pe-5 py-1"/>
                    </div>
                    <div className="mt-3">
                    <button type="button" className="btn btn-warning text-white rounded-pill px-5 py-2 mt-3">BOOK NOW</button>
                    </div>

                </form>
            </div>
            <div className="col-12 col-md-6">
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3783.48963895849!2d73.87657337431942!3d18.506762769597596!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2c037bae8e857%3A0x381c90ea14a6f8b9!2sBurger%20-%20Camp%20Branch!5e0!3m2!1sen!2sin!4v1695672976540!5m2!1sen!2sin" width="600" height="380" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
            </div>
        </div>
        </div>


    );
}

export default Book; 