

const Menu1  = (props)  =>
{
return(
    <div className="row ">
    <div className="col-12 ">
        <div className="card ">
        <div className="imgbg text-center">
    <img src= {props.image}  className="img-fluid cardimg mx-auto py-5 "  alt="image not found"/>
        </div>
      <div class="card-body cardbg py-4 rounded">
       
        <h5 className="card-title fw-bold text-light font2">{props.header}</h5>
        <p className="card-text text-light font2">{props.content}</p>
        <p className="text-white  fs-5 me-auto">$18  <span className="text-center"><i class="fas fa-shopping-cart text-warning"></i></span> </p>
      </div>
    </div>
    </div>
    

    </div>
);
}

export default Menu1;
